*{
    margin: 0;
    box-sizing: border-box;
}
a:hover{
    opacity: 0.5;
}
@font-face {
    font-family: 'TiemposHeadline';
    src: url('./assets/font/TiemposHeadline-Semibold.woff2') format('woff2'),
        url('./assets/font/TiemposHeadline-Semibold.woff') format('woff');
    font-display: swap;
}

$primaryFont: 'Outfit', sans-serif;
$secondaryFont: 'TiemposHeadline';


@mixin primaryFamily($weight, $size, $height) {
    font-family: $primaryFont;
    font-weight: $weight;
    font-size: $size;
    line-height: $height;
  }
@mixin secondaryFamily($size, $height) {
    font-family: $secondaryFont;
    font-size: $size;
    line-height: $height;
  
  }


.search{
    @include primaryFamily(400, 16px, 24px);
}
.welcome{
    @include primaryFamily(700, 14px, 24px);
}
.explore{
    @include primaryFamily(700, 32px, 42.56px);
}
.marble{
    @include primaryFamily(500, 14px, 24px);
    letter-spacing: 5%;
}
.follow{
    @include primaryFamily(700, 24px, 28.8px);
}
.view{
    @include primaryFamily(600, 14px, 18.62px);
}
.link{
    @include primaryFamily(600, 12px, 19.2px);
}
.featured{
    @include primaryFamily(600, 12px, 15.96px);
}
.website{
    @include primaryFamily(500, 13px, 20.8px);
}
.name{
    @include primaryFamily(500, 14px, 18.62px);
}
.inspire{
    @include primaryFamily(600, 14px, 22.4px);
}
.icon{
    @include primaryFamily(600, 10px, 16px);
}
.new{
    @include primaryFamily(600, 13px, 20.8px);
}
.body{
    @include primaryFamily(400, 14px, 22.4px);
}
.content{
    @include primaryFamily(500, 11px, 14.63px);
}
.h2{
    @include primaryFamily(700, 24px, 31.92px);
}
.h3{
    @include primaryFamily(700, 18px, 23.94px);
}
.h4{
    @include primaryFamily(700, 16px, 21.28px);
}
.h5{
    @include primaryFamily(700, 14px, 18.62px);
}
.secH1{
    @include secondaryFamily(48px, 63.84px);
}
.secH3{
    @include secondaryFamily(24px, 31.92px);
}
.s1{
    @include primaryFamily(500, 24px, 38.4px);
}
.s3{
    @include primaryFamily(500, 16px, 21.28px);
}
.s4{
    @include primaryFamily(500, 14px, 22.4px);
}
.s5{
    @include primaryFamily(500, 12px, 15.96px);
}
@media (max-width: 1199.98px) { 
    .secH1{
        @include secondaryFamily(24px, 33.84px);
    }
    .secH3{
        @include secondaryFamily(24px, 31.92px);
    }
}
@media (max-width: 767.98px) { 
    .secH1{
        @include secondaryFamily(32px, 42.56px);
    }
}

.card-head{
    display: flex;
    align-items: flex-start;

    .logo{
        align-items: center;
        display: flex;
    }
    .text{
        margin-left: 10px;
        display: flex;

        h2{
            color: #02022B;
        }
        p{
            color:#F27059;
        }
        .btn1{
            margin: 5px 10px;
            border-radius: 13px;
            padding: 2px 12px;
            border: 2px solid  #02022B;
            a{
                text-decoration: none;
                color:  #02022B;
                padding: auto;
            }
        }
    }
    .right{
        display: flex;
        flex-grow: 1;
        justify-content: end;
        align-items: center;

        .btn2{
            padding: 4px 12px;
            margin-top: 5px;
            border-radius: 4px;
            background: linear-gradient(48.91deg, #FFAA47 0%, #F6B28D 20.99%, #FF6090 72.54%, #F27059 98.94%);

            a{
                text-decoration: none;
                color: white;
            }
        }
        .dots{
            display: flex;
            margin-left: 10px;
            .circle{
                background: #CFD2E0;
                width: 4px;
                height: 4px;
                border-radius: 2px;
                margin-right: 2px;
            }

        }
    }
}
.card-body{
   margin-top: 20px;

   .s1{
       margin: 5px 0px;
   }
   .website{
       display: flex;
       margin: 20px 0px;
  
       .left{
           display: flex;
           flex:1;
           .web-icon{
               margin-right: 10px;
               margin-top: 3px;
           }
       }
       .right{
            flex:1;
            display: flex;
            justify-content: end;
            .hi-icon{
                margin-right: 20px;
            }
       }
   }


   .line{
    border: 1px solid #E7E7E7;
    margin: 30px 0px;
   }


   .comment-head{
       margin: 20px 0px;
       display: flex;

     img{
        height: 24px;
        width: 24px;
     }
     .name{
         margin: 3px 10px;
     }
     .view{
        color:#F27059;
     }
   }
   .comment-body{
       display: flex;
       
       .secH3{
           border-left: 2px solid #F27059;
          
          margin-left: 10px;    
          padding-left: 20px;     
       }
   }



   .inspired{
    display: flex;
    margin-bottom: 10px;
    
    .left{ 
        display: flex;
        flex: 1;
        align-items: center;

        .inspire{
            margin-left: 10px;
            color:#323F55;
        }
        .chat{
            margin-left: 20px;
            color:#323F55;
        }
    }
     .right{
         display: flex;
         flex: 1;
         justify-content: end;
         align-items: center;

         .inspire{
             margin-left: 5px;
             color: #797F99;
         }
         .book{
             margin-left: 10px;
            color: #797F99;
         }
         .eye{
            color: #797F99;
         }
     }
  }
}

