.image-card1{
     max-width:325px;   
     margin: auto;
     position: relative;
     margin-bottom: 10px;

  

    .card_image{
        width: 320px;
        height: 457px;
     
    }
.overlay{
    background: linear-gradient(to bottom, transparent 0%, rgb(23, 20, 20) 100%);
    position: absolute;
    top:0px;
    width: 320px;
    height: 457px;
    border-radius: 20px;
}
     .new-img{
 
         border-top-right-radius: 12px;
         border-bottom-left-radius: 12px;
         position: absolute;
         background-color: rgba(255, 255, 255, 0.8);
         
         top: 2%;
         right:4%;
         .new{
            padding: 5px 10px;
           
         } 
     }
     .s3{
         color: #F27059;
         position: absolute;
         top: 75%;
         left:4%;
     }
     .h2{
         color: #ffffff;
         position: absolute;
         top: 80%;
         left:4%;
     }
     .btn1{
         border-radius: 13px;
         padding: 2px 12px;
         border: 2px solid  #ffffff;
         position: absolute;
         top: 90%;
         left:4%;
         a{
             text-decoration: none;
             color:  #ffffff;
             padding: auto;
         }
       
     
     }
    
      
 }