 .container {
    max-width: 600px;
    display: flex;
    align-items:center ;

    .group {
       
        padding: 12px 8px;
        padding-right: 20px;
       margin-right: 10px;
        background-color: #02022B;
        border-radius: 32px;
       
    
      }
      .h4{
          color:white;
      }
      input[type="checkbox"] {
        display: none;
      }
      label {
        position: relative;
        margin-left: 40px;
      }
      label::before {
        content: "";
        background: url("../../../public/images/Circular.svg");
        background-position: center;
        background-size: contain;
        width: 20px;
        height: 20px;
        position: absolute;
        left: -30px;
        top: 0px;
      
        transform: scale(0) rotateZ(180deg);
        transition: all 0.4s cubic-bezier(0.54, 0.01, 0, 1.49);
      }
      
      input[type="checkbox"]:checked + label::before {
        transform: scale(1) rotateZ(0deg);
      }
      
      label::after {
        content: "";
        border: 2px solid #F27059;
        width: 20px;
        height: 20px;
        position: absolute;
        left: -32px;
        top: -2px;
        border-radius: 50%;
      }
      .group:last-child{
        background-color: rgb(255, 255, 255);
        border: 1px solid #CFD2E0;
        
        .h4{
            color:#797F99;
        }
        label::before {
          background: url("../../../public/images/Circular1.svg");
        }
        label::after{
          border: 2px solid  #CFD2E0;
        }
    }
    
  }
  
 
  
  
  
 
