.minor-card{
    max-width: 355px;

  margin-bottom: 20px;
   margin-left: 10px;
   margin-right: 10px;
   padding: 15px 20px;
   box-shadow: 0px 10px 10px 0px rgba(21, 20, 20, 0.3);
    border-radius: 20px;

    .card-head{
        margin-bottom: 20px;
        .logo{
            width: 40px;
            height: 40px;
            margin-top: 2px;
        }
        .right{
            margin-top: 10px;
        }
    }
    .card-body{
        .img-grid{
            display: flex;
            img{
                width: 151px;
                height: 151px;
                margin-right: 10px;
            }
        }
        .img-grid2{
            display: flex;
            img{
                width: 151px;
                height: 151px;
                margin-right: 10px;
               margin-top: 10px;
            }
        }
        .s3{
            margin: 20px 0px;
        }
        .line{
            margin: 25px 0px;
        }
        
        
        .inspired{
            .left{
                
                .inspire{
                    margin-left: 5px;
                }
                .chat{
                    margin-left: 10px;
                }
            }
            .right{
                .book{
                    margin-left: 8px;
                }
            }
        }
        
        
       
    }
    
}