.first_main_card{
    margin: auto;
    margin-top: 50px;
    max-width: 1210px;
    display: flex;

   
   .first_left_card{
       max-width: 740px;
       margin-left: 70px;

       .box1{
        width: 770px;
        display: flex;
     
          .box1_card{
             margin-left: 10px;
           }
        }
       .box2{
           display: flex;

          .box2_card{
              margin-left: 20px;
              margin-right: 20px;
              margin-top: -220px;
     
            

              .small{
                margin-top: 20px;
                padding-top: 20px;
           
              } 
           }  
       }
    }
   .first_right_card{
     max-width: 375px; 
     margin-left: 20px;
     display: flex;
     flex-direction: column;

      .box4{
        width: 352px ;
        box-shadow: 0px 10px 20px 1px rgba(0, 0, 0, 0.3);
        border-radius: 20px;
        margin :10px auto;
        
      }
    }

   @media (max-width: 1199.98px) { 
      flex-direction: column;
      align-items: center;

      .first_left_card{
          .box1{
            flex-direction:column;
            margin-top: 20px;
            align-items: center;
            margin-left: 0px;
            .box1_card{
                margin: 0px;
            }
            
          }
          .box2{
            flex-direction:column;
            align-items: center;
            
            .box2_card{
                margin: 0px;
            }
          }
      }
      .first_right_card{
          flex-direction:row ;
          flex-wrap: wrap;
          margin-left: 0px;
          .box4{
              margin: 0px;
              margin-top: 20px;
          }
      }

   }
   @media (max-width: 991.98px) { 
    .first_left_card{
        margin-left: 10px;
       
    }
 }
}

.second_main_card{
  
    margin:  auto;
    margin-top: 0px;
    max-width: 1210px;
    display: flex;

    .second_left_card{
        max-width: 375px;
        margin-left: 70px;
        .box5{
            margin-top: -80px;
        }
    }
    .second_right_card{
        max-width: 740px;
    
        .box6{
            display: flex;
        }
        .box7{
            .box7_card{
                margin-top: -110px;
            }
            display: flex;
        }
    }
    @media (max-width: 1199.98px) { 
        flex-direction: column;
        align-items: center;
        .second_left_card{
            margin-left: 0px;
            .box5{
                margin-top: 20px;
            }
        }
        .second_right_card{
            .box6{
                flex-direction:column;
                align-items: center;
                margin-top: 20px;
            }
            .box7{
                flex-direction:column;
                align-items: center;
                .box7_card{
                    margin-top: 20px;
                }
            }
        }
     }
    
}