.major-card{
  
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px;
    margin-bottom: 20px;
    padding: 20px;
    box-shadow: 0px 10px 10px 0px rgba(21, 20, 20, 0.3);
    border-radius: 20px;


    
     @media (max-width: 767.98px) { 
        max-width: 355px;

        margin-bottom: 20px;
      margin: auto;
         padding: 15px 20px;
          box-shadow: 0px 10px 20px 1px rgba(0, 0, 0, 0.3);
          border-radius: 20px;
 
        .card-head{
            margin-bottom: 20px;
            .text{
                .btn1{
                    display: none;
                }
            }
            .logo{
                width: 40px;
                height: 40px;
                margin-top: 2px;
            }
            .right{
                margin-top: 10px;
                .btn2{
                    display: none;
                }
            }
        }
        .card-body{

            .website{
                .left{
                    img{
                        width: 12px;
                        height: 16px;
                    }
                }
            }
            img{
                width: 312px;
                height: 312px;
            }
            .img-grid{
                display: flex;
                img{
                    width: 151px;
                    height: 151px;
                    margin-right: 10px;
                }
            }
            .img-grid2{
                display: flex;
                img{
                    width: 151px;
                    height: 151px;
                    margin-right: 10px;
                   margin-top: 10px;
                }
            }
            .s3{
                margin: 20px 0px;
            }
            .line{
                margin: 25px 0px;
            }
            
            
            .inspired{
                .left{
                    img{
                        width: 12px;
                        height: 16px;
                    }
                    .inspire{
                        margin-left: 5px;
                    }
                    .chat{
                        margin-left: 10px;
                    }
                }
                .right{
                    .book{
                        margin-left: 8px;
                    }
                }
            }
            
            
           
        }
      }
}

.hide{
    display: none;
}
.hideIns{
    display: none;
}