 .dropdown {
     max-width: 130px;
  
     margin: auto 20px;
   
     user-select: none;
     position: relative;
  

    .dropdown-btn {
      
        color: #333;
      
      padding: 5px;
        
       
        cursor: pointer;

        .drop-icon{
          
          margin-bottom: -3px;
          
        }
   
      }
      .dropdown-content {
        position: absolute;
        top: 100%;
        left: 0px;
   
     
     
        color: #333;

        .dropdown-item {
            padding: 10px;
            cursor: pointer;
            transition: all 0.2s;
          }
          .dropdown-item:hover {
            background: #F27059;
            color:white;
     
          }
          
      }

  }

  

 