.explore{
    max-width: 1090px;
    margin: 50px auto;
    text-align: center;
    padding: 5px;
  

}

.Feed{
    max-width: 1250px;
    margin: auto;

    margin-bottom: 50px;
    margin-top: 30px;
    display: flex;
    flex-direction: column;

    
   
    
    .box{
    
        display: flex;
        
      
        .logo{
            margin-right: 40px;
            margin-top: 5px;
        }
        .explore-mob{
          display: none;
        }
        
        .box-feed{
            width: 1050px;
     
         
        
            box-shadow: 5px 10px 20px 1px rgba(176, 173, 173, 0.3);
            border-radius: 48px;
            display:flex;
            
            
           .left{
             flex:2;
               display: flex;
            
           }
           .right{
            margin-right: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
               .searchbar{
           

                  border-radius: 48px;

              

                  .search-icon{
                    margin-left: 20px;
                    margin-right: 5px;
                    padding-top: 3px;
             
                  }
                 .search{
                  border: none;
                 
                  border-radius: 48px;
                  padding-left: 10px;
              
                  }
                  .search:focus-visible{
                    outline: none;
                  }
           
                  }
                }
            }
       }
       .mob{
        display: none;
        text-align: center;
        margin-top: 20px;
        .menu{
          display: flex;
          justify-content: center;

          .back{
            background-color: rgba(254, 166, 117, 0.3);
            padding:4px ;
            margin-right: 20px;
            a{
             
              color: #F27059;
            }
          }
          a{
            text-decoration: none;
            padding: 4px 12px;
            color:#999999;
          }
        }
        .content{
          margin-top: 20px;
          color:#999999;
        }
      }
       .main{
        margin-left: 120px;
        margin-top: 50px;
        display: grid;
  
        grid-template-columns: 1fr 5fr ;
     
    

        .slide-card.slide-head{
           padding: 19px 24px;
           box-shadow: 5px 10px 20px 1px rgba(0, 0, 0, 0.3);
           width: 160px;
           height: 240px;
           .marble{
             color:#F27059;
             margin-bottom: 30px;
           }
           
           .view{
             margin-top: 37px;
             a{
               color:#F27059;
             }
           }
        }
        .slide-card{ 
          border-radius: 12px;
          margin-right: 20px;
        

          .Swiper{
           
            .swiper-wrapper{
              flex-direction: column;
              
            }
          }
        }
       .slide-card:last-child{
         margin-right: 0px;
       } 
       .SwiperSlide{
        display: grid;
        
        grid-template-columns: repeat(auto-fit, 11rem);
       }
       .swiper-button-prev::after {
        display: none;
      }
    }
    @media (max-width: 1199.98px) { 
      margin-left: 20px;
      .box{
        .logo{
          margin-right: 0px;
        }
      }
    }
    @media (max-width: 991.98px) { 
      .box{
        .box-feed{
          .right{
            display: none;
          }
        }
      }
      .main{
        margin-left: 0px;
      }
    }
    @media (max-width: 767.98px) {
      .SwiperSlide{
        justify-content: center;
      }
      .box{
        margin: auto;
       
        .explore-mob{
          display: block;
          margin-top: 0px;
          margin-bottom: 0px;
       
        }
        
        .box-feed{
            display: none;
        }
      }
      .main{
        margin-top: 0px;
        display: block;
        .slide-card.slide-head{
          width: 100%;
        height: auto;
          text-align: center;
        }
      }
     }
     @media (max-width: 575.98px) { 
      flex-direction: column-reverse;
      .slide-card.slide-head{
        display: none;
      }
      .box{
        margin-top: 30px;
      }
      .mob{
        display: block;
      }
      .main{
        .slide-card{
          .SwiperSlide{
            display: flex;
            
          }
          
        }
      }
      .swiper-button-next::after {
        display: none;
      }
     }
}
@media (max-width: 767.98px) {

  .explore{
    display:none;
  }
}