.audio-card{

    max-width: 355px;

    margin-bottom: 20px;
     margin-left: 10px;
     margin-right: 10px;
     padding: 15px 20px;
      box-shadow: 0px 10px 20px 1px rgba(0, 0, 0, 0.3);
      border-radius: 20px;
     

    .card-head{
        margin-bottom: 20px;
        .logo{
            width: 40px;
            height: 40px;
            margin-top: 2px;
        }
        .right{
            margin-top: 10px;
        }
    }
    .card-body{
        .music{
            display: flex;
            margin-top: 10px;
            .audio-text{
                margin-left: 20px;
                margin-top: 10px;
                .s5{
                    color:#797F99;
                    padding-right: 20px;
                    padding-top: 10px;
                }
            }
        }
        audio::-webkit-media-controls-mute-button {
            display: none !important;
        }
        
        audio::-webkit-media-controls-volume-slider {
            display: none !important;
        }
        .img-grid{
            display: flex;
            img{
                width: 151px;
                height: 151px;
                margin-right: 10px;
            }
        }
        .img-grid2{
            display: flex;
            img{
                width: 151px;
                height: 151px;
                margin-right: 10px;
               margin-top: 10px;
            }
        }
        .s3{
            margin: 20px 0px;
        }
        .line{
            margin: 25px 0px;
        }
        
        
        .inspired{
            .left{
                .inspire{
                    margin-left: 5px;
                }
                .chat{
                    margin-left: 10px;
                }
            }
            .right{
                .book{
                    margin-left: 8px;
                }
            }
        }
        
        
       
    }
}