.header{
    max-width: 1440px;
    margin: auto;
    padding: 18px 0px;
    display: grid; 
    grid-template-columns: 3rem 1fr 1fr 1fr 3rem;
  .logo{
    display: grid;
    align-items: center;
    grid-column: 2/3;
  }
  .second{
    display: grid;
    justify-content: center;
    
  .searchbar{
    max-width: 238px;
    border: 1px solid #CFD2E0;
    border-radius: 48px;
    display: grid;
    grid-template-columns: 1fr 4fr ;
    align-items: center;
    padding: 10px;

   .search-icon{
      margin-right: 5px;
    }
   .search{
    border: none;
    margin-right: 20px;
    }
    .search:focus-visible{
      outline: none;
    }
  }
}
  .notify{
    display: flex;
    justify-content: end;
    align-items: center;
   .circle{
       height: 40px;
       width: 40px;
       border-radius: 20px;
       margin-left: 20px;
   }
  }
  .mobile-nav{
      display: none;
  }
  @media (max-width: 767.98px) {
    .second{
        .searchbar{
            max-width: 180px;
            .search{
                max-width: 140px;
            }
        }
    }
}
@media (max-width: 767.98px) {
    grid-template-columns: 1rem 1fr 2fr 1rem;
    padding: 14px 0px;

    .second{
        display: none;
    }
    .notify{
        display: none;
    }
    .mobile-nav{
        display: flex;
       
        justify-content: flex-end;
        align-items: center;
        align-items: e;
       .link{
           margin-left: 20px;
           text-decoration: none;
           color: black;
       }
       .dark{
        background: #02022B;
        margin-left: 20px;
        border-radius: 48px;
        
        
        .link{
            color: white;
            margin:20px; 
           
        }
       }
    }
}
}

