.background{

    margin: auto;

    background-image: url("../../assets/images/BannerBackground.svg");
    background-size: cover;
    display: grid; 
    grid-template-columns:  1fr 1fr 1fr ;

  .left{

  display: flex;
  .img1{
      margin-right: 15px;
      border-top-left-radius: 0px;
      border-bottom-left-radius: 0px;
  }
  .img2{
    margin-right: 15px;
    margin-top: 40px;
}
  img{
      margin: 30px 0px;
      border-radius: 20px;
  }
  }
  .center{
  
    color: white;
text-align: center;
 p{
     color: #F27059;
     margin-top: 56px;
     margin-bottom: 20px;
 }
    h1{
        max-width: 414px;
        margin: auto;
      
        text-align: center;
        color: white;
  
    }
  }
  .right{
 
    display: flex;
    justify-content: flex-end;
    .img5{
        margin-left: 15px;
        margin-top: 20px;
    }
    .img6{
        margin-left: 15px;
        border-top-right-radius: 0px;
        border-bottom-right-radius: 0px;
    }
    img{
        margin: 30px 0px;
        border-radius: 20px;
    }
  }
  @media (max-width: 1199.98px) { 
      .center{
          padding: 20px;
      }
  }
  @media (max-width: 991.98px) { 
      overflow: hidden;
    .left{
        .img1{
            display: none;
        }
    } .center{
        p{
            margin-top: 20px;
        }
    }
    .right{
        .img6{
            display: none;
        }
    }
  }
  @media (max-width: 767.98px) { 
    grid-template-columns:  1fr 2fr 1fr  ;
    .left{
        .img2{
            display: none;
        }
    } 
    .right{
        .img5{
            display: none;
        }
    }
  
   }
   @media (max-width: 575.98px) { 
    grid-template-columns:  1fr  ;
    .left{
        .img3{
            display: none;
        }
    } 
    .center{
        h1{
            max-width: 278px;
         
        }
        p{
            margin-top: 10px;
        }
    }
    .right{
        .img4{
            display: none;
        }
    }
   }
}
