.slider{
  position: relative;
  margin-right: 15px;
  border-radius: 20px;

  height: 250px;
.main-image{
  box-shadow: 0px 10px 10px 0px rgba(21, 20, 20, 0.3);
  border-radius: 20px;
}
  .button {
    border: 0;
    background: transparent;
    box-sizing: border-box;
    width: 0;
    height: 20px;
    position: absolute;
    top:42%;
    left:46%;
    border-color: transparent transparent transparent #ffffff;
    transition: 100ms all ease;
    cursor: pointer;
  
    // play state
    border-style: solid;
    border-width: 17px 0 17px 30px;
  
  
  }
  .logo{
      height: 40px;
      width: 40px;
      position: absolute;
      border-radius: 20px;
      top:65%;
      left:6%;
      border-radius: 20px;
      border: 2px solid white;
  }
  .h5{
     position: absolute;
     top:86%;
      left:6%;
      color: #ffffff;
  }

  @media (max-width: 575.98px) { 
    height: 60px;

    width: 100px;
  
    .main-image{
      display: none;
      height: 0px;
    }
    .logo{
      position: absolute;
      top:0%;
      left:15%;
      border: 2px solid rgb(0, 0, 0);
  }
  .h5{
     position: absolute;
     top:70%;
      left:0%;
      bottom:10%;
      color: #000000;
  }

  }
}
